import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { TabsPage } from './tabs.page';

const routes: Routes = [
  {
    path: 'tabs',
    component: TabsPage,
    children: [
      {
        path: 'forms',
        children: [
          {
            path: '',
            loadChildren: () =>
              import('../forms/forms.module').then(m => m.FormsPageModule)
          }
        ]
      },
      {
        path: 'documents',
        children: [
          {
            path: '',
            loadChildren: () =>
              import('../documents/documents.module').then(m => m.DocumentsPageModule)
          }
        ]
      },
      {
        path: 'submissions',
        children: [
          {
            path: '',
            loadChildren: () =>
              import('../submissions/submissions.module').then(m => m.SubmissionsPageModule)
          }
        ]
      } ,
      {
        path: '',
        redirectTo: '/tabs/forms',
        pathMatch: 'full'
      },     
    ]
  },
  {
    path: '',
    redirectTo: '/tabs/forms',
    pathMatch: 'full'
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class TabsPageRoutingModule {}
